    h3.t-green.mb-4(data-aos="fade") Identificacin y caracterizacin de los peligros y las amenazas
    p(data-aos="fade") Se entiende por peligro a una fuente o situacin que tiene el potencial de causar dao en trminos de lesin o enfermedad, dao a la propiedad, al ambiente de trabajo, al medio ambiente o una combinacin de estos. Por otro lado, la amenaza se refiere a la probabilidad de que un fenmeno de origen natural o humano cause dao y genere prdidas en un tiempo y lugar determinado.

    .row.align-items-center
      .col-lg-7.mb-4(data-aos="fade-right")
        p En la identificacin de peligros, la organizacin debe realizar una observacin y estudio detallado de los procesos, las actividades que realiza y su entorno, indicando cules son aquellas situaciones que pueden generar una emergencia. Una vez identificados los peligros, stos deben ser analizados segn su probabilidad de ocurrencia en trminos de amenaza.
        p.bg-green.p-4 Para identificar los posibles peligros, se deben analizar y determinar aspectos como la combinacin o mezcla de sustancias qumicas y los residuos con caractersticas peligrosas, con el almacenamiento o transporte de gases txicos o lquidos corrosivos, la inflamabilidad de una sustancia, la presencia de materiales radiactivos, el deterioro de la va, los cuales, pueden interactuar con peligros de origen natural y social, como por ejemplo, la presencia de una falla geolgica, de ros, de volcanes, las condiciones atmosfricas adversas en la zona, o las condiciones polticas y sociales de la regin.
