    h3.t-green.mb-4(data-aos="fade") Anlisis de vulnerabilidad por amenaza

    .row.align-items-center
      .col-lg-7.mb-4(data-aos="fade-right")
        p El anlisis de vulnerabilidad es el proceso por el cual se determina el nivel de exposicin y predisposicin a la prdida de un elemento o grupo de elementos frente a una amenaza especfica. El grado de vulnerabilidad de una empresa ante una amenaza determinada est directamente relacionado con su capacidad interna para prevenir o controlar los factores que originan el peligro, as como con su preparacin para minimizar las consecuencias en caso de que ocurra un evento adverso.
