    h3.t-green.mb-4(data-aos="fade") Definicin de los escenarios de riesgo
    p.bg-green8.p-4.mb-5(data-aos="fade") Se entiende como la descripcin de un futuro posible y de la trayectoria asociada a l. El escenario de riesgo es la interaccin de los diferentes factores de riesgo (amenaza y vulnerabilidad) en un territorio y en un momento dado. Debe describir y permitir la identificacin del tipo de dao y prdidas que pueden generarse en caso de presentarse un evento peligroso en unas condiciones dadas de vulnerabilidad.


