    h3.subt.text-white.mb-4(data-aos="fade") Control de plagas

    p.mb-4(data-aos="fade") El control de plagas o manejo de plagas tiene el objetivo de regular una especie a travs de diferentes tipos de medidas, para poder comprender es necesario revisar algunos conceptos:   

    .row.mb-4
