          b cause dao o molestias, o que transmita o provoque una enfermedad. 
          | Las plagas pueden ser animales, plantas o bacterias. Una plaga tambin puede ser cualquier cosa que simplemente se encuentre en un lugar no deseado, por ejemplo, el trbol en una zona de juegos cubierta por csped. Las ratas, los ratones, las cucarachas, las moscas domsticas, los mapaches, las ardillas, las hormigas, la maleza y las bacterias son diferentes tipos de plagas. Antes de intentar eliminarlas, es importante asegurarse de que realmente se trate de una plaga, ya que no todas las plagas son vectores que causen enfermedades. Algunas plagas simplemente se encuentran en un lugar donde no deberan estar.
        p Es importante tener en cuenta que existen algunas plagas que no se deben matar ni utilizar pesticidas para su exterminio, como los murcilagos, las palomas y las ardillas, mientras que otras s se pueden eliminar con pesticidas, como las cucarachas y las ratas.
      .col-lg-4.col-md-5.bg-white.pt-2(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido.mb-0
