    h3.subt.text-white.mb-4(data-aos="fade") Tcnicas de desinfeccin y limpieza

    p.mb-4(data-aos="fade") Antes de ahondar en las tcnicas de desinfeccin es necesario conocer la diferencia de los siguientes conceptos.

    .row.mb-5(data-aos="fade-down")
