          .tarjeta.bg-green9
            .p-4.p-md-5.pt-md-4.text-center
              h5 Solucin
              p  Combinacin de un slido o de un producto concentrado con agua, para obtener una distribucin homognea de cada uno de los componentes.


    h3.subt.text-white.mb-4(data-aos="fade") Modelo genrico de limpieza y desinfeccin manual

    .row.align-items-center.mb-5
      .col-lg-7.mb-4(data-aos="fade-right")
        p 
          b La limpieza 
          | es una tarea fundamental en cualquier establecimiento donde se manipulen alimentos. Esta consiste en la eliminacin gruesa de la suciedad, para lo cual se utilizan tcnicas como el raspado, frotado, barrido o pre-enjuagado de las superficies. Posteriormente se realiza la 
          b limpieza principal, 
          | que implica la aplicacin de un detergente para desprender la suciedad de las superficies. Una vez completada esta etapa, se procede al 
          b  enjuague, 
          | que consiste en el arrastre de la suciedad desprendida y la solucin de detergente por medio del agua potable. 
        p Para garantizar una higiene completa, es necesario realizar 
          b la desinfeccin, 
          | que implica la destruccin de los microorganismos mediante el uso de un desinfectante adecuado, dejando actuar el tiempo necesario segn corresponda. Finalmente, se realiza un enjuague final para eliminar los restos del desinfectante y se procede al secado para eliminar cualquier resto de agua. Es importante asegurarse de que todas estas etapas se lleven a cabo de manera rigurosa para garantizar la seguridad alimentaria.
      .col-lg-5.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema8/img2.png', alt='')



    h3.subt.text-white.mb-4(data-aos="fade") Descontaminacin y limpieza cuarto de almacenamiento de residuos
    p.mb-4(data-aos="fade") La descontaminacin y limpieza del cuarto de almacenamiento de residuos es una tarea importante en la gestin de residuos, ya que permite prevenir la propagacin de infecciones y enfermedades. A continuacin, se presentan algunos pasos recomendados para descontaminar y limpiar el cuarto de almacenamiento de residuos:

    SlyderF.mb-5.text-center.d-lg-flex.d-none(columnas="col-md-6 col-lg-4" data-aos="fade-down")
      .tarjeta.bg-green5.p-4
        h5.mb-3 Antes de la descontaminacin
        p.text-center Si dentro del cuarto se encuentran residuos, el procedimiento de descontaminacin limpieza y desinfeccin no debe llevarse a cabo hasta que el gestor autorizado los haya retirado.  
      .tarjeta.bg-green5.p-4
        h5.mb-3 Descontaminacin
        p.text-center Descontaminar el cuarto (techo, paredes y piso) y los contenedores con hipoclorito a 5.000 ppm durante 20 minutos inmediatamente despus de que el gestor autorizado retire los residuos el da establecido de cada semana.  
      .tarjeta.bg-green5.p-4
        h5.mb-3 Enjuague de contenedores y cuartos
        p.text-center El cuarto y contenedores deben enjuagarse con suficiente agua potable, proveniente de una manguera con suficiente presin, de modo que el agua la cubra totalmente. En caso de no poder utilizar una manguera, el agua debe estar contenida en recipientes como baldes completamente limpios.
      .tarjeta.bg-green5.p-4
        h5.mb-3 Lavado de las superficies
        p.text-center Enjabonar las superficies a limpiar del cuarto (techo, paredes y piso) as como los contenedores y traje de proteccin personal, esparciendo la solucin de jabn con un cepillo suave para barrer. Utilice otro cepillo pequeo para el traje de proteccin personal.
      .tarjeta.bg-green5.p-4
        h5.mb-3 Uso del detergente
        p.text-center El jabn o detergente no debe aplicarse directamente sobre las superficies a limpiar, sino que ste debe disolverse previamente en agua potable.
      .tarjeta.bg-green5.p-4
        h5.mb-3 Eliminacin de residuos
        p.text-center Una vez que todo lo anteriormente descrito est en contacto con el jabn diluido, se procede a refregar las superficies eliminando completamente todos los residuos que puedan estar presentes en ellas. Muchas veces estos residuos no son muy visibles, por esta razn la operacin debe ser hecha concienzudamente de modo que toda el rea que est siendo tratada quede completamente limpia.
      .tarjeta.bg-green5.p-4
        h5.mb-3 Enjuague
        p.text-center La superficie se deja en contacto con el jabn por un periodo de cinco minutos. Realizar el enjuague final con suficiente agua potable, proveniente de una manguera con presin, de modo que el agua arrastre totalmente el jabn. No utilice esponjas o telas en el proceso de enjuague, ya que pueden contener jabn o estar sucias.
      .tarjeta.bg-green5.p-4
        h5.mb-3 Revisin
        p.text-center Despus del enjuague se debe hacer una revisin visual para verificar que ha sido eliminada toda la suciedad. En caso de necesitar se debe hacer de nuevo un lavado con jabn hasta que la superficie quede completamente limpia.

    .row.d-flex.d-lg-none(data-aos="fade-down")
      .col-md-6.mb-3
