    h3.subt.text-white.mb-4(data-aos="fade") Desinfectante hipoclorito de sodio (NaOCl)

    p.mb-4(data-aos="fade") Es el desinfectante usado por excelencia en los procesos de desinfeccin en recipientes y cuartos de almacenamiento de residuos.

    .row.align-items-center.mb-md-3
