          li
            i.fas.fa-check
            | En general se utiliza en forma de hipoclorito sdico, excelente desinfectante, bactericida, virucida. Es inestable y disminuye su eficiencia en presencia de luz, calor y largo tiempo de preparacin, por lo tanto, la presentacin comercial indicada son envases oscuros y no transparentes.
          li
            i.fas.fa-check
            | Es altamente corrosivo por lo tanto no debe usarse por ms de treinta minutos, ni repetidamente en material de acero inoxidable.
          li
            i.fas.fa-check
            | Es un qumico econmico, asequible, de gran aplicabilidad y se consigue comercialmente en forma lquida a una concentracin entre el 4% y el 6%.
