    #t_5_2.titulo-segundo.color-acento-contenido(data-aos="fade")
      h2 5.2 Caractersticas de los recipientes

    p(data-aos="fade") Los tipos y capacidades de los recipientes dependen de las caractersticas y tipos de residuos, el tipo y frecuencia del sistema de recoleccin y del espacio disponible para ellos.
    p.mb-4(data-aos="fade") Frecuentemente se emplean bolsas, contenedores, canecas, cajas, entre otros. Sin embargo, se debe tener en cuenta lo siguiente:
  
    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-10.bg-blue2.p-4.p-md-5
        .row.align-items-center.b-bottom.pb-4
          .col-lg-1.col-md-2.col-4.mb-3.mb-lg-0
            img(src='@/assets/curso/temas/tema5/5-2-ico1.svg', alt='') 
          .col-lg-11.col-md-10
            p.mb-0 #[b A nivel domstico] se espera que los recipientes sean de material impermeable, liviano y resistente, de fcil carga, de tal forma que facilite su transporte y se reduzca el impacto sobre el ambiente y la salud humana.
        .row.align-items-center.b-bottom.py-4
          .col-lg-1.col-md-2.col-4.mb-3.mb-lg-0
            img(src='@/assets/curso/temas/tema5/5-2-ico2.svg', alt='') 
          .col-lg-11.col-md-10
            p.mb-0 #[b Los recipientes de carcter retornable] deberan permitir su fcil limpieza.
        .row.align-items-center.b-bottom.py-4
          .col-lg-1.col-md-2.col-4.mb-3.mb-lg-0
            img(src='@/assets/curso/temas/tema5/5-2-ico3.svg', alt='') 
          .col-lg-11.col-md-10
            p.mb-0 #[b Los recipientes que se empleen para presentar los residuos para recoleccin] deberan evitar el contacto de los residuos con el entorno y con las personas encargadas de dicha recoleccin.
        .row.align-items-center.b-bottom.py-4
          .col-lg-1.col-md-2.col-4.mb-3.mb-lg-0
            img(src='@/assets/curso/temas/tema5/5-2-ico4.svg', alt='') 
          .col-lg-11.col-md-10
            p.mb-0 #[b Los recipientes tanto desechables como retornables deberan:] proporcionar seguridad e higiene, permitir el aislamiento de los residuos con el entorno, tener una capacidad y volumen proporcional al peso, volumen y caractersticas de los residuos contenidos, ser de material resistente, preferiblemente reciclable, reutilizable o ambos y facilitar su cierre o amarre.
        

    p.mb-5(data-aos="fade") Los recipientes para residuos infecciosos deben ser del tipo tapa y pedal, las canecas se identifican con el logo de infeccioso, si es qumico algunas veces colocan el logo de una calavera y dicen el tipo de qumico que se envasa. 

    .row.justify-content-center.align-items-center.mb-5(data-aos="fade-down")
