        p La resistencia de las bolsas debe soportar la tensin ejercida por los residuos contenidos y por su manipulacin. 
        p El material plstico de las bolsas para residuos infecciosos, debe ser polietileno de alta densidad, o el material que se determine necesario para la desactivacin o el tratamiento de estos residuos. 
