        p Los colores de bolsas seguirn el cdigo establecido por la organizacin y/o entidad territorial, sern de alta densidad y calibre mnimo de 1.4 para bolsas pequeas y de 1.6 milsimas de pulgada para bolsas grandes, suficiente para evitar el derrame durante el almacenamiento en el lugar de generacin, recoleccin, movimiento interno, almacenamiento central y disposicin final de los residuos que contengan.
